<template>
  <div class="navBar">
    <div class="contentBox">
      <div class="logoBox">
        <img src="../img/logo1.png" alt="">
        <span>号召力</span>
      </div>
      <div class="aBox"><a :href="'#page' + (i+1)" class="texts" :class="{white: index-1 === i}" v-for="(text, i) in texts">{{text}}</a>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      index: {
        type: Number
      }
    },
    data () {
      return {
        texts: ['网站首页', '产品服务', '区块链', '关于我们']
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .navBar {
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: rgba(0, 0, 0, .55);
  }

  .contentBox {
    width: 1000px;
    margin: auto;
    line-height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    .logoBox {
      img {
        width: 66px;
        height: 66px;
        vertical-align: middle;
      }
      span {
        vertical-align: middle;
        line-height: 66px;
        color: #fff;
        font-size: 30px;
      }
    }
    .aBox {
      display: flex;
      flex: 0 0 445px;
      width: 445px;
      justify-content: space-around;
      .texts, .texts:link, .text:visited {
        text-decoration: none;
        color: rgb(153, 153, 153);
        font-size: 20px;
      }
      .white {
        color: #fff;
      }
    }
  }
</style>



