<template>
  <div class="banner">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide bg1">
          <div class="contentBox">
            <h1>区块链</h1>
            <span>基于区块链技术的资产数字化平台</span>
            <label>Digital assets platfrom based on block chain</label>
          </div>
        </div>
        <div class="swiper-slide bg2">
          <div class="contentBox">
            <h1>"链"上全球，共生共赢</h1>
          </div>
        </div>
        <div class="swiper-slide bg3">
          <div class="contentBox"></div>
        </div>
      </div>
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Swiper from '../../static/js/swiper.min.js';
  export default {
    mounted () {
      this.initSwiper();
    },
    methods: {
      initSwiper () {
        // eslint-disable-next-line
        var mySwiper = new Swiper('.swiper-container', {
          autoplay: 3000,
          loop: true,
          pagination: '.swiper-pagination',
          paginationClickable: true
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import "../../static/css/swiper.min.css";

  .banner {
    width: 100%;
    height: 100%;
  }

  .swiper-container {
    width: 100%;
    height: 100%;
    .swiper-wrapper {
    }
    .swiper-pagination-bullet {
      background: rgb(150, 190, 210);
      &-active {
        background: #fff;
      }
    }
  }

  .center {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1000px;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }

  .bg1 {
    position: relative;
    background: no-repeat url(../img/banner1.png);
    background-size: 100% 100%;
    .contentBox {
      .center();
      h1 {
        color: #fff;
        font-size: 210px;
      }
      span {
        color: #fff;
        font-size: 65px;
      }
      label {
        color: rgb(178, 204, 220);
        font-size: 45px;
      }
    }
  }

  .bg2 {
    background: no-repeat url(../img/banner2.png);
    .contentBox {
      .center();
    }
  }

  .bg3 {
    background: no-repeat url(../img/banner3.png);
    .contentBox {
    }
  }
</style>



