<template>
  <div class="imgText">
    <div class="contentBox" :style="boxStyle">
      <img :src="src" :style="imgStyle">
      <h1>{{mytitle}}</h1>
      <div>{{text}}</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      src: String,
      mytitle: String,
      text: String,
      boxW: {type: String, required: true},
      imgW: {type: String, required: true},
      imgH: String
    },
    data () {
      return {
        boxStyle: {
          width: this.boxW
        },
        imgStyle: {
          width: this.imgW,
          height: this.imgH
        }
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .imgText {
  }

  .contentBox {
    width: 210px;
    text-align: center;
    img {
    }
    h1 {
      margin: 10px 0 10px;
      color: #000;
      font-size: 20px;
      font-weight: 400;
    }
    div {
      color: rgb(153, 153, 153);
      font-size: 16px;
    }
  }
</style>



