<template>
  <div id="app">
    <nav-bar :index="index"></nav-bar>
    <leave-msg></leave-msg>
    <contact></contact>
    <div class="fullPage">
      <div data-anchor="page1" class="section">
        <page1></page1>
      </div>
      <div data-anchor="page2" class="section">
        <page2></page2>
      </div>
      <div data-anchor="page3" class="section">
        <page3></page3>
      </div>
      <div data-anchor="page4" class="section">
        <page4></page4>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import $ from 'jquery';
  import 'fullPage.js';

  import NavBar from '@/components/navBar.vue';
  import LeaveMsg from '@/components/leaveMsg.vue';
  import Contact from '@/components/contact.vue';
  import Page1 from './views/page1banner.vue';
  import Page2 from './views/page2service.vue';
  import Page3 from './views/page3blockchain.vue';
  import Page4 from './views/page4aboutus.vue';

  export default {
    name: 'app',
    components: {NavBar, Page1, Page2, Page3, Page4, LeaveMsg, Contact},
    mounted () {
      this.initFullPage();
    },
    data () {
      return {
        index: 1
      };
    },
    methods: {
      initFullPage () {
        let that = this;
        $('.fullPage').fullpage({
          scrollBar: true,
          anchors: ['page1', 'page2', 'page3'],
          onLeave: function (index, nextIndex, direction) {
            that.index = nextIndex;
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../src/assets/css/jquery.fullPage.css';

  #app {
    position: relative;
  }
</style>



