<template>
  <div class="imgText2">
    <img class="img" :src="src" :style="imgStyle" alt="">
    <div class="textBox">
      <h1>{{mytitle}}</h1>
      <div>{{text}}</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      src: String,
      imgW: String,
      imgH: String,
      mytitle: String,
      text: String
    },
    data () {
      return {
        imgStyle: {
          width: this.imgW,
          height: this.imgH
        }
      };
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .imgText2 {
    width: 450px;
    display: flex;
    align-content: flex-start;
    margin-bottom: 80px;
    .img {
      width: 80px;
      height: 75px;
    }
    .textBox {
      padding-left: 18px;
      h1 {
        font-size: 20px;
        font-weight: 300;
      }
      div {
        font-size: 16px;
      }
    }
  }
</style>



