<template>
  <div class="lineHeader">
    <i class="line"></i>
    <span class="text">{{text}}</span>
    <i class="line"></i>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      text: String
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .lineHeader {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    width: 1000px;
    padding: 120px 12px 30px;
    .line {
      display: block;
      flex: 1 1;
      height: 1px;
      background: rgb(212, 220, 220);
    }
    .text {
      padding: 0 24px;
      font-size: 30px;
    }
  }
</style>



