import Vue from 'vue'
import VueResource from 'vue-resource'
import md5 from 'md5'
import utf8 from 'utf8'
import base64 from 'base-64'

Vue.use(VueResource)
// http相关
Vue.http.options.emulateJSON = true
Vue.http.options.xhr = {withCredentials: true}

var appId = ''
var appSecret = ''
if (process.env.NODE_ENV === 'production') {
  appId = '1493037951465'
  appSecret = 'aa5009503ead3a60180779f1d1db3227'
} else {
  appId = '1497350266621'
  appSecret = 'bc54f4d60f1cec0f9a6cb70e13f2127a'
}

// 接口预处理
Vue.http.interceptors.push(function (request, next) {
  // 请求时对请求进行加密
  try {
    if (process.env.NODE_ENV !== 'production') {
      console.log(request.body)
    }
    // 判断用户是否登录
    console.log({tokenUserId: 1, token: md5(appId + appSecret), ...request.body})
    let json = utf8.encode(JSON.stringify({tokenUserId: 1, token: md5(appId + appSecret), ...request.body}))
    var data = base64.encode(json)
  } catch (e) {
    console.log(e)
  }
  request.body = {jsonString: data}
  next(function (response) {})
})

const API_ROOT = (process.env.NODE_ENV === 'production') ? 'api.haozhaoli510.com/api' : 'http://120.76.188.42:8090/api'
export const resource = Vue.resource(API_ROOT + '?action={action}')
export const otherslogin = Vue.resource()



