<template>
  <div class="leaveMsg">
    <header>请您留言</header>
    <form class="contentBox">
      <textarea placeholder="请在此输入留言内容，我们会尽快与您联系。" v-model="content"></textarea>
      <div class="lines name">
        <span>姓名</span>
        <input type="text" placeholder="最多20个字符" v-model="name">
      </div>
      <div class="lines tel">
        <span>电话</span>
        <input type="text" placeholder="请输入您的手机号码（纯数字）" v-model="tel">
      </div>
      <div class="lines address">
        <span>地址</span>
        <input type="text" placeholder="最多100个字符" v-model="address">
      </div>
      <div class="lines mail">
        <span>邮箱</span>
        <input type="text" placeholder="请输入合法邮箱名" v-model="mail">
      </div>
      <button class="btn" @click="ajaxLeavingMsg">发送</button>
    </form>
  </div>
</template>

<script type="text/ecmascript-6">
  import api from '@/api';
  export default {
    data () {
      return {
        content: '',
        name: '',
        tel: '',
        address: '',
        mail: ''
      };
    },
    computed: {
      msg () {
        return {
          content: this.content,
          name: this.name,
          mobile: this.tel,
          address: this.address,
          email: this.mail
        };
      }
    },
    methods: {
      checkContent () {
        if (this.content) {
          return true;
        } else {
          alert('请输入内容');
        }
      },
      checkName () {
        if (this.name) {
          return true;
        } else {
          alert('请输入您的名字');
        }
      },
      checkTel () {
        let reg = /\d{11}/;
        if (reg.test(this.tel)) {
          return true;
        } else {
          alert('请输入正确的11位手机号');
        }
      },
      checkAddress () {
        if (this.address) {
          return true;
        } else {
          alert('请输入您的地址');
        }
      },
      checkMeil () {
        let reg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        if (reg.test(this.mail)) {
          return true;
        } else {
          alert('请输入正确的邮箱');
        }
      },
      ajaxLeavingMsg () {
        if (this.checkContent() && this.checkName() && this.checkTel() && this.checkAddress() && this.checkMeil()) {
          api.saveLeavingMsg(this.msg).then((res) => {
            let result = res.body;
            if (result.isSuccess) {
              alert('发送成功，我们会尽快与您联系！');
            } else {
              alert(result.description);
            }
          });
        }
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .leaveMsg {
    z-index: 100;
    position: fixed;
    top: 500px;
    left: 2%;
    width: 260px;
    height: 320px;
    border-radius: 3px;
    overflow: hidden;
    -webkit-box-shadow: 0 0 10px #bbb;
    -moz-box-shadow: 0 0 10px #bbb;
    box-shadow: 0 0 10px #bbb;
    background: rgb(245, 245, 245);
    text-align: center;
  }

  header {
    display: inline-block;
    width: 100%;
    background: rgb(51, 153, 204);
    color: #fff;
    font-size: 16px;
  }

  .contentBox {
    display: inline-block;
    width: 200px;
    margin: auto;
    textarea {
      resize: none;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      width: 100%;
      height: 95px;
      border: 1px solid #ddd;
      margin-top: 15px;
    }
    .lines {
      display: flex;
      width: 100%;
      height: 20px;
      margin-top: 8px;
      border: 1px solid #ddd;
      font-size: 12px;
      span {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        height: 100%;
        padding: 0 0.25em;
        border-right: 1px solid #ddd;
        line-height: 20px;
      }
      input {
        flex: 1 0;
        padding-left: .5em;
        border: none;
      }
    }
    .btn {
      width: 92px;
      height: 25px;
      border: 0;
      border-radius: 3px;
      margin: 12px auto 0;
      background: rgb(51, 153, 204);
      text-align: center;
      color: #fff;
    }
  }
</style>



